.form__header {
  @include clear-indents;
  margin-bottom: 35px;
  font-family: $header-font;
  color: $header-text;
  font-weight: 400;
  font-size: 36px;
  line-height: 60px;
}

.form__desc {
  @include clear-indents;
  margin-right: 40px;
  margin-bottom: 39px;
  font-family: $header-font;
  color: $header-text;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
}

.form__main-info {
  @include clear-indents;
  margin-bottom: 23px;
}

.form__selection {
  @include clear-indents;
  margin-bottom: 31px;
  padding: 20px;
}

.form__contact {
  @include clear-indents;
  margin-bottom: 31px;
}

.form__comment {
  @include clear-indents;
  margin-bottom: 31px;
}

.form__add {
  @include clear-indents;
  margin-bottom: 34px;
}

.form__btn {
  @include clear-btn;
  padding: 11px;
  margin-bottom: 23px;
  width: 100%;
  font-size: 16px;
  line-height: 20px;
  background-color: $main-color;
  color: $alt-text;
  text-transform: uppercase;

  &:hover,
  &:focus {
    background-color: $hover-btn;
  }

  &:active {
    color: rgba($alt-text, 0.3);
  }
}

.form__legend {
  @include clear-indents;
  margin-bottom: 34px;
  text-align: center;
}
